<template>
  <q-layout view="LHh lpR lFf" class="bg-color">
    <q-drawer
      bordered
      show-if-above
      v-model="containerSideMenu"
      side="left"
      :width="300"
      :breakpoint="900"
      :overlay="$q.screen.width <= 800"
      class="bg-primary text-white q-px-md"
    >
      <q-scroll-area
        class="fit"
        :horizontal-thumb-style="{ opacity: 0 }"
        :vertical-thumb-style="{ opacity: 0 }"
      >
        <div class="q-pt-sm q-mt-xs q-pb-xl q-px-lg">
          <GrowmodoTextIcon
            dark
            class="cursor-pointer q-px-xs q-py-sm"
            @click="$root.goTo_Home"
          />
        </div>

        <q-stepper
          v-if="showSkeleton"
          v-model="step"
          animated
          vertical
          contracted
          :header-nav="false"
          header-class="text-body1"
          color="white"
          done-icon="icon-tick text-h4 text-secondary"
          done-color="white"
          active-icon="icon-circle-01 text-body2 text-secondary"
          active-color="primary-200"
          inactive-icon="icon-circle-01 text-body2 text-primary-200"
          inactive-color="primary"
          error-icon="icon-alert-triangle"
          error-color="negative"
          class="stepper-request bg-transparent rounded no-shadow stepper-dark"
        >
          <q-step
            v-for="j in [1, 2, 3, 4, 5]"
            :key="j"
            :name="j"
            title="Loading..."
            color="white"
          />
        </q-stepper>

        <q-stepper
          v-else
          v-model="step"
          animated
          vertical
          contracted
          :header-nav="false"
          header-class="text-body1"
          color="white"
          done-icon="icon-tick text-h4 text-secondary"
          done-color="white"
          active-icon="icon-circle-01 text-body2 text-secondary"
          active-color="primary-200"
          inactive-icon="icon-circle-01 text-body2 text-primary-200"
          inactive-color="primary"
          error-icon="icon-alert-triangle"
          error-color="negative"
          class="stepper-request bg-transparent rounded no-shadow stepper-dark"
        >
          <q-step
            v-for="(question, i) in dynamic_questions_array"
            :key="question"
            :name="i"
            color="white"
            :title="question.title"
            :done="step > i || request_submitted"
          />
        </q-stepper>
      </q-scroll-area>
    </q-drawer>

    <q-header class="bg-white">
      <q-toolbar class="bg-transparent q-pa-lg">
        <q-breadcrumbs
          active-color="primary-500"
          separator-color="primary-500"
          gutter="xs"
        >
          <template v-slot:separator>
            <q-icon size="xs" name="icon-chevron-right" class="q-py-xs" />
          </template>
          <q-breadcrumbs-el
            icon="icon-home-line"
            class="q-py-xs q-pr-sm cursor-pointer text-primary-500 hover-text-secondary"
            v-close-popup="-1"
            @click="$root.goTo_Home"
          />
          <q-breadcrumbs-el
            :label="`${isTask ? 'Task' : 'Project'} Directory`"
            class="q-py-xs q-px-sm rounded cursor-pointer hover-text-secondary text-primary-500 font-medium"
            v-close-popup
            @click="goTo_Directory()"
          />
          <q-breadcrumbs-el
            :label="requestInfo.title || `New ${isTask ? 'Task' : 'Project'}`"
            class="text-secondary bg-accent q-py-xs q-px-sm rounded font-medium"
          />
        </q-breadcrumbs>
        <q-space />
        <div v-if="false">
          <close-button @click="$root.goTo_Home" />
        </div>
      </q-toolbar>
    </q-header>

    <q-page-container>
      <q-page padding class="bg-white flex flex-center">
        <q-card flat style="margin-top: -100px">
          <q-card-section>
            <div style="max-width: 100vw; width: 650px">
              <q-form
                autocorrect="off"
                autocapitalize="off"
                autocomplete="off"
                spellcheck="false"
                @submit.prevent
              >
                <template v-if="!request_submitted">
                  <div v-if="showSkeleton" class="q-px-lg q-pb-md">
                    <q-skeleton type="QToolbar" width="70%" />
                    <q-skeleton width="45%" type="QBadge" class="q-my-md" />
                    <div class="row q-col-gutter-md">
                      <div class="col-6" v-for="j in [1, 2, 3, 4]" :key="j">
                        <q-skeleton type="QInput" />
                      </div>
                    </div>
                  </div>

                  <q-stepper
                    v-else
                    v-model="step"
                    flat
                    animated
                    header-nav
                    contracted
                    color="primary"
                    ref="formStepper"
                    header-class="hidden"
                    class="full-width"
                  >
                    <q-step
                      v-for="(question, i) in dynamic_questions_array"
                      :key="question"
                      :name="i"
                      :title="question.title"
                      color="white"
                      error-color="negative"
                      icon="step:inactive"
                      active-color="white"
                      active-icon="step:active"
                      done-icon="step:done"
                      done-color="white"
                      class="q-py-sm"
                    >
                      <div class="full-width text-left q-py-lg">
                        <div class="font-medium text-h4 q-py-sm">
                          {{ question.title }}
                        </div>
                        <div class="text-body2 text-primary-500">
                          {{ question.question }}
                        </div>
                      </div>
                      <div class="full-width">
                        <DynamicQuestions
                          :autofocus="true"
                          :question="question"
                          :initAnswer="answer[question.id]"
                          :platforms="requestInfo.platforms"
                          @answered="(ans) => (answer[question.id] = ans)"
                          :textareaRow="15"
                          :ref="
                            question.type.startsWith('upload_')
                              ? `upload${question.id}`
                              : undefined
                          "
                          :fileRemovedFile="
                            uploads[`upload${question.id}`]?.removedFile
                          "
                          :fileFactory="factoryFn"
                          :fileInitSlots="uploads[`upload${question.id}`]"
                          fileThumbnail="auto"
                          @checked="
                            (val) => (answer[question.id].value.info = val)
                          "
                          :checked="answer[question.id]?.value?.info"
                          @fileAdded="
                            (files) => {
                              if (
                                !uploads[`upload${question.id}`]?.files
                                  ?.length ||
                                question.type === 'website_content'
                              ) {
                                addUploadToModel(
                                  'uploads',
                                  `upload${question.id}`,
                                  files,
                                  `upload${question.id}`
                                );
                              } else if (
                                !uploads[`upload${question.id}`]?.files
                                  ?.length ||
                                question.type === 'upload_multiple'
                              ) {
                                addUploadToModel(
                                  'uploads',
                                  `upload${question.id}`,
                                  files,
                                  `upload${question.id}`
                                );
                              } else {
                                uploads[`upload${question.id}`].files = files;
                              }
                            }
                          "
                          @fileUploaded="
                            ({ files, xhr }) => {
                              const response = JSON.parse(xhr.response || '{}');
                              if (response.success) {
                                const data = response.data;
                                if (question.type === 'website_content') {
                                  answer[question.id].value.uploads = answer[
                                    question.id
                                  ].value.uploads.concat(data.map((e) => e.id));
                                } else if (
                                  question.type === 'upload_multiple'
                                ) {
                                  answer[question.id].value = answer[
                                    question.id
                                  ].value.concat(data.map((e) => e.id));
                                } else {
                                  answer[question.id].value = data[0]?.id;
                                }
                              }
                            }
                          "
                          @fileRemoved="
                            (files) => {
                              if (question.type === 'website_content') {
                                files
                                  .map((e) => e.id)
                                  .forEach((e) => {
                                    const ind = answer[
                                      question.id
                                    ]?.value.uploads?.findIndex?.(
                                      (a) => a === e
                                    );
                                    if (ind !== -1) {
                                      answer[question.id].value.uploads.splice(
                                        ind,
                                        1
                                      );
                                    }
                                  });
                              } else if (question.type === 'upload_multiple') {
                                files
                                  .map((e) => e.id)
                                  .forEach((e) => {
                                    const ind = answer[
                                      question.id
                                    ]?.value.uploads?.findIndex?.(
                                      (a) => a === e
                                    );
                                    if (ind !== -1) {
                                      answer[question.id].value.splice(ind, 1);
                                    }
                                  });
                              } else {
                                answer[question.id].value = '';
                              }
                            }
                          "
                        ></DynamicQuestions>
                      </div>

                      <!-- Alternative -->
                      <template
                        v-if="
                          question.alternative_if &&
                          (answer[question.id]?.value ===
                            question.alternative_if ||
                            answer[question.id]?.value?.value ===
                              question.alternative_if)
                        "
                      >
                        <div class="full-width text-left q-py-md">
                          <div
                            v-if="question.alternative_question"
                            class="text-body2 text-primary-500"
                          >
                            {{ question.alternative_question }}
                          </div>
                        </div>
                        <div class="full-width">
                          <DynamicQuestions
                            :question="{
                              id: question.id,
                              type: question.alternative_type,
                              required: question.required,
                              options: question.alternative_options,
                              placeholder: question.alternative_placeholder,
                            }"
                            :initAnswer="{
                              value: answer[question.id].alternative_answer,
                            }"
                            :platforms="requestInfo.platforms"
                            @answered="
                              (ans) =>
                                (answer[question.id].alternative_answer =
                                  ans?.value)
                            "
                            :textareaRow="15"
                            :ref="
                              question.alternative_type.startsWith('upload_')
                                ? `upload${question.id}`
                                : undefined
                            "
                            :fileRemovedFile="
                              uploads[`upload${question.id}`]?.removedFile
                            "
                            :fileFactory="factoryFn"
                            :fileInitSlots="uploads[`upload${question.id}`]"
                            fileThumbnail="auto"
                            @checked="
                              (val) => (answer[question.id].value.info = val)
                            "
                            :checked="answer[question.id]?.value?.info"
                            @fileAdded="
                              (files) => {
                                if (
                                  !uploads[`upload${question.id}`]?.files
                                    ?.length ||
                                  question.alternative_type ===
                                    'website_content'
                                ) {
                                  addUploadToModel(
                                    'uploads',
                                    `upload${question.id}`,
                                    files,
                                    `upload${question.id}`
                                  );
                                } else if (
                                  !uploads[`upload${question.id}`]?.files
                                    ?.length ||
                                  question.alternative_type ===
                                    'upload_multiple'
                                ) {
                                  addUploadToModel(
                                    'uploads',
                                    `upload${question.id}`,
                                    files,
                                    `upload${question.id}`
                                  );
                                } else {
                                  uploads[`upload${question.id}`].files = files;
                                }
                              }
                            "
                            @fileUploaded="
                              ({ files, xhr }) => {
                                const response = JSON.parse(
                                  xhr.response || '{}'
                                );
                                if (response.success) {
                                  const data = response.data;

                                  if (
                                    question.alternative_type ===
                                    'website_content'
                                  ) {
                                    answer[
                                      question.id
                                    ].alternative_answer.uploads = answer[
                                      question.id
                                    ].alternative_answer.uploads.concat(
                                      data.map((e) => e.id)
                                    );
                                  } else if (
                                    question.alternative_type ===
                                    'upload_multiple'
                                  ) {
                                    answer[question.id].alternative_answer =
                                      answer[
                                        question.id
                                      ].alternative_answer.concat(
                                        data.map((e) => e.id)
                                      );
                                  } else {
                                    answer[question.id].alternative_answer =
                                      data[0]?.id;
                                  }
                                }
                              }
                            "
                            @fileRemoved="
                              (files) => {
                                if (
                                  question.alternative_type ===
                                  'website_content'
                                ) {
                                  files
                                    .map((e) => e.id)
                                    .forEach((e) => {
                                      const ind = answer[
                                        question.id
                                      ]?.alternative_answer.uploads?.findIndex?.(
                                        (a) => a === e
                                      );
                                      if (ind !== -1) {
                                        answer[
                                          question.id
                                        ].alternative_answer.uploads.splice(
                                          ind,
                                          1
                                        );
                                      }
                                    });
                                } else if (
                                  question.alternative_type ===
                                  'upload_multiple'
                                ) {
                                  files
                                    .map((e) => e.id)
                                    .forEach((e) => {
                                      const ind = answer[
                                        question.id
                                      ]?.alternative_answer.uploads?.findIndex?.(
                                        (a) => a === e
                                      );
                                      if (ind !== -1) {
                                        answer[
                                          question.id
                                        ].alternative_answer.uploads.splice(
                                          ind,
                                          1
                                        );
                                      }
                                    });
                                } else {
                                  answer[question.id].alternative_answer = '';
                                }
                              }
                            "
                          ></DynamicQuestions>
                        </div>
                      </template>
                    </q-step>
                  </q-stepper>
                </template>

                <div v-else class="full-width q-px-lg q-py-sm">
                  <!-- Done -->
                  <div class="text-center">
                    <CardIconBox icon="icon-check-circle-broken" />
                  </div>
                  <div
                    class="full-width font-medium text-h4 q-pt-lg text-center"
                  >
                    Thank you.<br />Your {{ isTask ? 'task' : 'project' }} have
                    been successfully submitted.
                  </div>
                </div>

                <!-- Navigation Buttons -->
                <div
                  v-if="!showSkeleton"
                  class="row items-center justify-center q-px-lg q-pt-none q-mt-none q-col-gutter-md"
                >
                  <template v-if="!request_submitted">
                    <div v-if="step > 0" class="col">
                      <q-btn
                        outline
                        no-caps
                        unelevated
                        :ripple="{ color: 'accent' }"
                        color="primary"
                        class="rounded slide-hover full-width button-gm-outline-dark text-body1 font-medium button-lg"
                        :disable="request_submitting"
                        @click="previousStep()"
                      >
                        <q-icon
                          size="20px"
                          name="icon-arrow-narrow-left"
                          class="q-pr-xs"
                        />
                        Back
                      </q-btn>
                    </div>
                    <div
                      v-if="step === dynamic_questions_array.length - 1"
                      class="col"
                    >
                      <q-btn
                        no-caps
                        unelevated
                        :ripple="{ color: 'accent' }"
                        color="primary"
                        class="rounded slide-hover full-width text-body1 font-medium button-lg"
                        :disable="showSkeleton || request_submitting"
                        @click="submitRequest"
                      >
                        Submit {{ isTask ? 'Task' : 'Project' }}
                        <q-icon
                          size="20px"
                          name="icon-arrow-narrow-right"
                          class="q-pl-xs"
                        />
                      </q-btn>
                    </div>
                    <div v-else class="col">
                      <q-btn
                        no-caps
                        unelevated
                        :ripple="{ color: 'accent' }"
                        color="primary"
                        class="rounded slide-hover full-width text-body1 font-medium button-lg"
                        :disable="request_submitting"
                        @click="nextStep"
                      >
                        Continue
                        <q-icon
                          size="20px"
                          name="icon-arrow-narrow-right"
                          class="q-pl-xs"
                        />
                      </q-btn>
                    </div>
                  </template>
                  <template v-else>
                    <div class="col">
                      <q-btn
                        outline
                        no-caps
                        :ripple="{ color: 'accent' }"
                        color="primary"
                        class="rounded slide-hover full-width text-body1 font-medium button-gm-outline-dark button-lg"
                        @click="$root.goTo_Home"
                      >
                        <q-icon
                          size="20px"
                          name="icon-arrow-narrow-left"
                          class="q-pr-sm"
                        />
                        Back to home
                      </q-btn>
                    </div>
                    <div class="col">
                      <q-btn
                        no-caps
                        unelevated
                        :ripple="{ color: 'accent' }"
                        color="primary"
                        class="rounded slide-hover full-width text-body1 font-medium button-lg"
                        @click="goTo_Directory()"
                      >
                        Add your next request
                      </q-btn>
                    </div>
                  </template>
                </div>
              </q-form>
            </div>
          </q-card-section>
        </q-card>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<script>
import { defineComponent, ref, defineAsyncComponent } from 'vue';
import GrowmodoTextIcon from 'src/components/GrowmodoTextIcon.vue';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';
import {
  Editor_Mixin,
  FileUploader_Mixin,
  RequestOptions_Mixin,
} from 'src/mixins';
import { mapState, mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useApp from 'stores/app';
import {
  checkIfObject,
  extractErrorMessageString,
} from 'src/assets/scripts/functions.js';
import fnStore from 'stores/fn.store';

export default defineComponent({
  name: 'FormNewRequest',

  mixins: [RequestOptions_Mixin, FileUploader_Mixin, Editor_Mixin],

  components: {
    GrowmodoTextIcon,
    CardIconBox,
    DynamicQuestions: defineAsyncComponent(() =>
      import('src/components/DynamicQuestions.vue')
    ),
  },

  data() {
    return {
      step: -1,
      answer: ref({}),
      request_submitting: false,
      request_submitted: false,
      containerSideMenu: false,
      uploads: {},
      checkIfObject: checkIfObject,
    };
  },

  computed: {
    ...mapState(useGrowmodo, [
      'taskDirectory_Categories',
      'projectDirectory_Categories',
      'taskDirectory',
      'projectDirectory',
    ]),

    taskRouteId() {
      return this.$route.params?.taskId;
    },

    projectRouteId() {
      return this.$route.params?.projectId;
    },

    isTask() {
      return this.taskRouteId;
    },

    requestInfo() {
      return (
        (this.isTask
          ? this.taskDirectory[this.taskRouteId]
          : this.projectDirectory[this.projectRouteId]) || {}
      );
    },

    showSkeleton() {
      return (
        !this.dynamic_questions_array.length ||
        // Object.keys(this.answer).length !==
        //   this.dynamic_questions_array.length ||
        this.step === -1
      );
    },

    dynamic_questions_array() {
      return this.requestInfo.questions || [];
    },

    dynamic_questions() {
      const data = {};
      for (const question of this.dynamic_questions_array) {
        data[question.id] = question;
      }
      return data;
    },
  },

  watch: {
    ['taskRouteId']: {
      async handler(val) {
        if (val) {
          await this.init_declare();
          this.init();
        }
      },
    },
  },

  async mounted() {
    this.isTask
      ? await this.getTaskDirectory()
      : await this.getProjectDirectory();
    await this.init_declare();
    this.init();
  },

  methods: {
    ...mapActions(useGrowmodo, [
      'requestTask',
      'requestProject',
      'getTaskDirectory',
      'getProjectDirectory',
    ]),
    ...mapActions(useApp, ['init_RequestForm']),

    async init_declare() {
      this.step = -1;
      this.answer = ref({});
      this.request_submitting = false;
      this.request_submitted = false;
      this.uploads = ref({});
      // Back to Home if Not Task or Poject
      if (!this.requestInfo?.title) this.$root.goTo_Home();
    },

    async init() {
      this.$root.setMeta({ title: this.requestInfo.title });
      this.step = 0;
      this.init_RequestForm();
    },

    async submitRequest() {
      if (!this.validateData()) return;
      this.request_submitting = true;
      const payload = {
        title: this.requestInfo.title,
        [`${this.isTask ? 'task' : 'project'}_type_id`]:
          this.requestInfo.tags?.[0],
        [`${this.isTask ? 'task' : 'project'}_id`]: this.requestInfo.id,
        dynamic_questions: this.answer,
      };
      const response = this.isTask
        ? await this.requestTask(payload)
        : await this.requestProject(payload);
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        if (message)
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: message,
          });
        this.step++;
        this.request_submitted = true;
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Request submittion failed!',
        });
      }
      this.request_submitting = false;
    },

    factoryFn(files) {
      return new Promise((resolve, reject) => {
        const token = fnStore.getAuth();
        resolve({
          method: 'POST',
          fieldName: 'uploads[]',
          url: `${this.$config?.api?.baseURL}/file/temp`,
          headers: [{ name: 'Authorization', value: `Bearer ${token}` }],
          withCredentials: false,
        });
      });
    },

    toogleContainerSideMenu(force = null) {
      this.containerSideMenu = force !== null ? force : !this.containerSideMenu;
    },

    validateData() {
      if (
        !this.answer ||
        isNaN(this.step) ||
        !this.dynamic_questions ||
        !this.dynamic_questions_array[this.step]
      )
        return;
      // Question Information
      const question = this.dynamic_questions_array[this.step] || {};
      if (process.env.DEV_MODE)
        console.log(question.required ? 'Required' : 'Optional', 'Question');
      // Upload needs to be done
      let response = true;
      if (question.type.startsWith('upload')) {
        const uploads = this.uploads[`upload${question.id}`];

        for (const file of uploads?.files || []) {
          if (file.__status === 'uploading') {
            response = false;
            break;
          } else {
            response = true;
          }
        }
      }
      if (!response) {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'Upload is still in progress.',
        });
        return false;
      } else {
        // No problem If not Required
        if (question.required === false) return true;
        // If Required and no answer
        if (!this.answer[question.id]?.value) return false;
        // Check if alternative and is provided
        if (
          question.alternative_if &&
          question.alternative_if == this.answer[question.id]?.value?.value &&
          !this.answer[question.id]?.alternative_answer
        )
          return false;
        // If Some empty objects
        if (typeof this.answer[question.id]?.value === 'object') {
          if (
            JSON.stringify(this.answer[question.id]?.value) == '{}' ||
            JSON.stringify(this.answer[question.id]?.value) == '[]'
          )
            return false;
        }
        // If textarea2 can't just <br>
        if (
          question.type === 'textarea2' &&
          this.answer[question.id]?.value == '<br>'
        )
          return false;
        // If website content
        if (question.type === 'website_content') {
          if (!this.answer[question.id]?.value?.uploads?.length) {
            if (!this.answer[question.id]?.value?.info?.length) return false;
          }
        }
        // Log on debugging
        if (process.env.DEV_MODE)
          console.warn('Answer:', this.answer[question.id]);
        return true;
      }
    },

    previousStep() {
      this.$refs?.['formStepper']?.previous?.();
    },

    nextStep() {
      if (this.validateData()) this.$refs?.['formStepper']?.next?.();
    },

    gotoStep(step_no) {
      this.$refs?.['formStepper']?.goTo?.(step_no);
    },

    goTo_Directory(model) {
      if (!model) model = this.isTask ? 'task' : 'project';
      // Open Directory
      this.$root.toggleRequest(model == 'task' ? 'project' : 'task', false);
      this.$root.toggleRequest(model, true);
    },
  },
});
</script>
